
import React, { Component } from 'react'
import { urlRootWithoutApi } from '../../globalState/globalVariables';
import io from 'socket.io-client';

class MongoDB extends Component {
  // ws = new WebSocket(urlRoot);

  constructor() {
    super()
    this.state = {
      socket : io(urlRootWithoutApi),
      defaultState: "ahoj",
      fromExpress: {}

    }
  }

  componentDidMount() {
    // var socket = io('http://localhost:3001');
    this.state.socket.on('fromExpress', (data) => {
      console.log('fromExpress received: ',data);
      this.setState({ fromExpress: data  });
    });

    // socket.emit('fromReact', { first: 'React ahoj' });
  }

  handleButtonClick = (e) => {
    console.log("click");
    this.state.socket.emit('fromReact', { counter: 77 });
  }

  render() {
    return (
      <div>
        <h1>MongoDB page</h1>
        <div id="serverEventsList">
          <h5>registered server events:</h5>
          {/* <p>urlRoot: {urlRoot}</p> */}
          <p>urlRoot: http://localhost:3001</p>

          <p>
            <span>from Express: </span>
            <span>{JSON.stringify(this.state.fromExpress)}</span>
          </p>
          <button onClick={this.handleButtonClick}>button</button>
        </div>
      </div>
    )
  }
}

export default MongoDB;
